import { useEffect, useState } from 'react';
import io from 'socket.io-client';

const socket = io('http://localhost:3000');

const QuizGame = () => {
    const [roomId, setRoomId] = useState('');
    const [username, setUsername] = useState('');
    const [players, setPlayers] = useState([]);
    const [question, setQuestion] = useState('');
    const [answer, setAnswer] = useState('');

    useEffect(() => {
        socket.on('roomUpdate', (room) => {
            setPlayers(room.players);
        });

        socket.on('newQuestion', (question) => {
            setQuestion(question);
        });

        return () => {
            socket.off('roomUpdate');
            socket.off('newQuestion');
        };
    }, []);

    const joinRoom = () => {
        socket.emit('joinRoom', { roomId, username });
    };

    const sendAnswer = () => {
        socket.emit('sendAnswer', { roomId, answer });
    };

    return (
        <div>
            <h1>Quiz Game</h1>
            <input type="text" placeholder="Room ID" value={roomId} onChange={(e) => setRoomId(e.target.value)} />
            <input type="text" placeholder="Username" value={username} onChange={(e) => setUsername(e.target.value)} />
            <button onClick={joinRoom}>Rejoindre</button>

            <h2>Joueurs dans la room</h2>
            <ul>
                {players.map(player => (
                    <li key={player.id}>{player.username}</li>
                ))}
            </ul>

            {question && (
                <div>
                    <h2>Question : {question}</h2>
                    <input type="text" placeholder="Votre réponse" value={answer} onChange={(e) => setAnswer(e.target.value)} />
                    <button onClick={sendAnswer}>Envoyer la réponse</button>
                </div>
            )}
        </div>
    );
};

export default QuizGame;
